.success-icon {
  align-self:center;
  height: 1.2rem;
  width: 2.3rem;
  border-color: green;
  border-style: solid;
  border-width: 0  0 .4rem .4rem;
  border-radius: .3rem 0 .3rem .3rem;
  animation: swipe .3s linear 1,
             swipe .3s linear 3s 1 forwards;
  animation-direction: normal, reverse;
  transform: translate(0px, -5px) rotate(-45deg);
}

@keyframes swipe {
  0% {
    opacity: 0%;
    transform:  translate(5px, -5px) rotate(-45deg);
  }
}